<template>
  <el-container>
    <!-- <div v-wechat-title="$route.meta.title">门诊缴费</div> -->

    <!--  -->
    <el-header style="height: 42px;">
      <div class="grid-content fonttop">
        <span>就诊人：</span>
        <span>*振</span>
        <span style="margin:10px">0005674</span>
        <span style="float: right;line-height: 15px;">
          <el-button class="cut">切换</el-button>
        </span>
      </div>
    </el-header>
    <!--  -->
    <el-main>
      <div class="title">
        <div class="title-img"></div>
        <div class="title-font">
          <span>待缴费单</span>
        </div>
      </div>
      <!--  -->
      <el-card class="box-card">
        <div>
          <el-row>
            <el-col :span="8" class="fontr">
              <div class="grid-content bg-purple">单据号</div>
              <div class="grid-content bg-purple">开单科室</div>
              <div class="grid-content bg-purple">开单人</div>
              <div class="grid-content bg-purple">开单日期</div>
            </el-col>
            <el-col :span="16" class="fontc">
              <div>
                <div class="grid-content bg-purple-light">
                  <span>{{Charge.SerialNumber}}</span>
                </div>
                <div class="grid-content bg-purple-light">
                  <span>{{Charge.Dept}}</span>
                </div>
                <div class="grid-content bg-purple-light">
                  <span>{{Charge.DocName}}</span>
                </div>
                <div class="grid-content bg-purple-light">
                  <span>{{Charge.ChargeDate}}</span>
                </div>
              </div>
            </el-col>
          </el-row>
          <el-divider style="height:.5px"></el-divider>
          <el-row>
            <el-col :span="6">
              <div class="grid-content bg-purple">总金额:</div>
            </el-col>
            <el-col :span="12">
              <div class="grid-content bg-purple-light">
                <span style="color:#FF7800;font-size: 17px;">￥186.00</span>
              </div>
            </el-col>
            <el-col :span="6">
              <div class="grid-content bg-purple-light">
                <router-link tag="a" to="/shopcart/Outpatient/OutpatientTopay">
                  <el-button type="primary" class="pay">去缴费</el-button>
                </router-link>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-card>
      <!--  -->
    </el-main>
  </el-container>
</template>
<style lang="stylus" scoped>
@import '../../assets/css/outpatient.css';
</style>
<script>
import axios from "axios";
export default {
  name: "Ment",
  data() {
    return {
      showCharge: "",
      Charge: []
    };
  },
  //mounted直接调用
  mounted() {
    axios
      .get("../../static/json.json")
      // .then(response => (this.info = response))
      .then((response)=>{
        //  var tmdate = new Date(parseInt(Datestamp.substr (10))); 
        this.Charge = response.data.data;
        // console.log(response.data.data.ChargeDate);
        var tmdate = response.data.data.ChargeDate;
        var time = tmdate.substr(10);
        console.log(time);
      })
      .catch((error)=>{
        console.log(error);
      });
  }
};
</script>




